import { Button, Card, Input, Paper } from "@mui/material";
import axios from "axios";
import React, { useContext, useEffect, useRef, useState } from "react";
import Message from "../../components/Message";
import { GlobalState } from "../../Context/Context";
import { serverURL } from "../../lib/lib";
import socket from "../../lib/web-socket";
function Chat() {
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const [first, setFirst] = useState(true);
  const { user } = useContext(GlobalState);
  const sendMessageHandler = (e) => {
    e.preventDefault();
    let name = user ? user.firstName : "Guest";
    socket.emit("sendMessage", { name, message });
    setMessage("");
  };
  const getMessages = async () => {
    try {
      const response = await axios.get(`${serverURL}/messages`);
      console.log("called in messages");
      setMessages(response.data.messages);
    } catch (err) {
      console.log(err.response);
    }
  };

  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    socket.on("newMessage", (data) => {
      setMessages((prev) => [...prev, data]);
      scrollToBottom();
    });

    return () => socket.off("newMessage");
  }, [messages]);
  useEffect(() => {
    getMessages();
  }, []);
  return (
    <>
      <Paper
        style={{
          height: "93vh",
          zIndex: "1",
          background: "white",
          borderRadius: "5px",
          opacity: ".9",
          display: "flex",
          flexDirection: "column",
          width: "50%",
          flexGrow: 1,
        }}
      >
        <div style={{ flex: "1", width: "50%", overflowY: "scroll" }}>
          {messages.length
            ? messages.map((message) => {
                return (
                  <Message
                    key={message._id}
                    message={message.message}
                    name={message.name}
                  />
                );
              })
            : null}
          <div ref={messagesEndRef}></div>
        </div>
        <div
          style={{
            display: "flex",
            width: "100%",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <form style={{ width: "100%" }} onSubmit={sendMessageHandler}>
            <Input
              style={{
                width: "90%",
                color: "black",
                fontWeight: "400",
                fontSize: "1.5rem",
              }}
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              placeholder="Send Us A Message!"
              required
            />
            <Button style={{ color: "black" }} type={"submit"}>
              Send
            </Button>
          </form>
        </div>
      </Paper>
    </>
  );
}

export default Chat;
